<template>
  <li class="blogitem" @click="readblog">
    <h4 class="blogitem-title">{{item.title}}</h4>
    <p class="blogitem-time">{{item.time}}</p>
    <p class="blogitem-texttop">{{item.texttop}}</p>
  </li>
</template>

<script>
export default {
  name: 'blogItem',
  props: ['item'],
  methods: {
    readblog () {
      this.$router.push(`/blog/${this.item.id}`)
    }
  }
}
</script>

<style lang="less" scoped>
  .blogitem-color{
    color: #fff;
    -webkit-text-shadow: 0 0 10px #333;
    -moz-text-shadow: 0 0 10px #333;
    text-shadow: 0 0 10px #333;
  }
  .blogitem{
    background-color: #607D8B;
    overflow: hidden;
    margin-bottom: 1px;
    padding-bottom: 10px;
    cursor: pointer;
  }
  .blogitem .blogitem-title{
    border-left:10px solid #02a8f5;
    margin: 6px;
    text-align: left;
    padding-left: 4px;
    font-size: 1.2rem;
    .blogitem-color
  }
  .blogitem .blogitem-time,.blogitem-texttop{
    text-align: left;
    padding-left: 20px;
    font-size: .9rem;
    .blogitem-color
  }
</style>



