            '</style>  \n' +
            '  <style type="text/css" id="export_setting_css">body { width: 100%; margin: 0px; padding: 0px;}</style>  \n' +
            '\n' +
            '<div id="export_content"><div class="output_wrapper" id="output_wrapper_id">\n' +
            '<p>有很多网站有反爬虫机制，例如封 ip。对于这个情况，可能需要改自己的 ip 才行，不太懂，所以不讨论这个。今天要介绍的是简单的爬虫（没有反爬的网站）。</p>\n' +
            '<p>首先要建一个 node 项目：</p>\n' +
            '<h1 id="hnpminit"><span>npm init</span></h1>\n' +
            '<p>init 不多介绍了。</p>\n' +
            '<p>这次抓包需要用到的包写在 package.json 中（要自己一个个安装也可），下面是 package.json 部分代码：</p>\n' +
            '<pre><code class="hljs cpp">{<br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="hljs-comment">/*&nbsp;省略其他&nbsp;*/</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="hljs-string">"dependencies"</span>:&nbsp;{<br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="hljs-string">"cheerio"</span>:&nbsp;<span class="hljs-string">"^1.0.0-rc.2"</span>,<br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="hljs-string">"bufferhelper"</span>:&nbsp;<span class="hljs-string">"^0.2.1"</span>,<br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="hljs-string">"iconv-lite"</span>:&nbsp;<span class="hljs-string">"^0.4.19"</span>,<br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="hljs-string">"request"</span>:&nbsp;<span class="hljs-string">"^2.83.0"</span><br>&nbsp;&nbsp;&nbsp;&nbsp;}<br>}<br></code></pre>\n' +
            '<p>写好了 dependencies 之后，执行：</p>\n' +
            '<h1 id="hnpminstall"><span>npm install</span></h1>\n' +
            '<p>在写爬虫之前，先介绍一下安装的这几个包的作用：</p>\n' +
            '<p>（1）cheerio：        用于解析 html，用法类似 jQuery；<br>（3）bufferhelper：用于辅助缓存；<br>（2）iconv-lite：     用于页面的转码；<br>（3）request：    用于发起请求，爬图片的需要使用。只是爬文字的话，可以不用。</p>\n' +
            '<p>然后就可以开始写爬虫代码了，新建一个 crawler.js（别的名字也行）。代码如下（实际使用需要做一些改动）：</p>\n' +
            '<pre><code class="hljs javascript"><span class="hljs-keyword">var</span>&nbsp;http&nbsp;=&nbsp;<span class="hljs-built_in">require</span>(<span class="hljs-string">\'http\'</span>);<br><span class="hljs-keyword">var</span>&nbsp;fs&nbsp;=&nbsp;<span class="hljs-built_in">require</span>(<span class="hljs-string">\'fs\'</span>);<br><span class="hljs-keyword">var</span>&nbsp;cheerio&nbsp;=&nbsp;<span class="hljs-built_in">require</span>(<span class="hljs-string">\'cheerio\'</span>);<br><span class="hljs-keyword">var</span>&nbsp;iconv&nbsp;=&nbsp;<span class="hljs-built_in">require</span>(<span class="hljs-string">\'iconv\'</span>);<br><span class="hljs-keyword">var</span>&nbsp;BufferHelper&nbsp;=&nbsp;<span class="hljs-built_in">require</span>(<span class="hljs-string">\'bufferhelper\'</span>);<br><br><span class="hljs-keyword">var</span>&nbsp;url&nbsp;=&nbsp;<span class="hljs-string">\'http://www.xxxx.com/xxx1.html\'</span>;&nbsp;<span class="hljs-comment">//&nbsp;要爬取的链接地址</span><br><br><span class="hljs-comment">//&nbsp;主程序</span><br><span class="hljs-function"><span class="hljs-keyword">function</span>&nbsp;<span class="hljs-title">main</span>&nbsp;(<span class="hljs-params">url</span>)&nbsp;</span>{<br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="hljs-comment">//&nbsp;使用&nbsp;http&nbsp;模块向服务器发起一次&nbsp;get&nbsp;请求</span><br>&nbsp;&nbsp;&nbsp;&nbsp;http.get(url,&nbsp;<span class="hljs-function"><span class="hljs-keyword">function</span>&nbsp;(<span class="hljs-params">res</span>)&nbsp;</span>{<br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="hljs-keyword">var</span>&nbsp;bufferHelper&nbsp;=&nbsp;<span class="hljs-keyword">new</span>&nbsp;BufferHelper();<br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="hljs-comment">//&nbsp;监听data事件，每次取一块数据</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;res.on(<span class="hljs-string">\'data\'</span>,&nbsp;<span class="hljs-function"><span class="hljs-keyword">function</span>&nbsp;(<span class="hljs-params">chunk</span>)&nbsp;</span>{<br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;bufferHelper.concat(chunk);<br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;})<br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;.on(<span class="hljs-string">\'end\'</span>,&nbsp;<span class="hljs-function"><span class="hljs-keyword">function</span>&nbsp;(<span class="hljs-params"></span>)&nbsp;</span>{<br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="hljs-keyword">var</span>&nbsp;allChunk&nbsp;=&nbsp;bufferHelper.toBuffer();<br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="hljs-keyword">var</span>&nbsp;decodeHTML&nbsp;=&nbsp;iconv.decode(allChunk,&nbsp;<span class="hljs-string">\'GB2312\'</span>);&nbsp;<span class="hljs-comment">//&nbsp;根据目标网页的编码格式转码。默认是&nbsp;utf-8，其他编码中文会变成乱码</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="hljs-keyword">var</span>&nbsp;$&nbsp;=&nbsp;cheerio.load(decodeHTML);&nbsp;<span class="hljs-comment">//&nbsp;使用&nbsp;cheerio&nbsp;模块解析&nbsp;html</span><br><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="hljs-comment">//&nbsp;这里就可以开始获取想爬的内容了，要寻找页面的规律来做，如：class、id、文字内容等</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="hljs-keyword">var</span>&nbsp;title&nbsp;=&nbsp;$(<span class="hljs-string">\'h1\'</span>).text();<br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="hljs-keyword">var</span>&nbsp;content&nbsp;=&nbsp;$(<span class="hljs-string">\'#content\'</span>).text();<br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;saveFile(title,&nbsp;content);<br><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="hljs-comment">/*&nbsp;<br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;不是只爬一个页面的话，可以将下一个参数传入，再次调用&nbsp;main(url)，不展开去写了；<br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;新的&nbsp;url&nbsp;最好也是从页面中爬出来，或者根据一定的规律在程序中写好，让其自动执行；<br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;如果每次要单独去弄，爬虫的意义何在？&nbsp;&nbsp;<br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;*/</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="hljs-keyword">var</span>&nbsp;nextLink&nbsp;=&nbsp;<span class="hljs-string">\'http://www.xxxx.com/xxx2.html\'</span>;&nbsp;<span class="hljs-comment">//&nbsp;这里应该是动态获取，或者已知的设定好的下一位</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;main&nbsp;(nextLink);&nbsp;<span class="hljs-comment">//&nbsp;这里不能像我这么写，否则就无限循环进去了，要设置何时终止</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;})<br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;.on(<span class="hljs-string">\'error\'</span>,&nbsp;<span class="hljs-function"><span class="hljs-keyword">function</span>&nbsp;(<span class="hljs-params">err</span>)&nbsp;</span>{<br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="hljs-built_in">console</span>.log(err);<br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;});<br>&nbsp;&nbsp;&nbsp;&nbsp;});<br>}<br><br><span class="hljs-comment">//&nbsp;保存文件，文件将保存在当前目录的&nbsp;data&nbsp;文件夹下</span><br><span class="hljs-function"><span class="hljs-keyword">function</span>&nbsp;<span class="hljs-title">saveFile</span>&nbsp;(<span class="hljs-params">title,&nbsp;content</span>)&nbsp;</span>{<br>&nbsp;&nbsp;&nbsp;&nbsp;fs.appendFile(<span class="hljs-string">\'./data/\'</span>&nbsp;+&nbsp;title&nbsp;+&nbsp;<span class="hljs-string">\'.txt\'</span>,&nbsp;content,&nbsp;<span class="hljs-string">\'utf-8\'</span>,&nbsp;<span class="hljs-function"><span class="hljs-keyword">function</span>&nbsp;(<span class="hljs-params">err</span>)&nbsp;</span>{<br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="hljs-keyword">if</span>&nbsp;(err)&nbsp;{<br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="hljs-built_in">console</span>.log(err);<br>&nbsp;&nbsp;&nbsp;&nbsp;}&nbsp;<span class="hljs-keyword">else</span>&nbsp;{<br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="hljs-built_in">console</span>.log(<span class="hljs-string">\'爬到了：\'</span>&nbsp;+&nbsp;title);<br>&nbsp;&nbsp;&nbsp;&nbsp;}<br>}<br><br><span class="hljs-comment">//&nbsp;保存图片，文件保存在当前目录的&nbsp;img&nbsp;文件夹下；图片不止一张，这个函数肯定是要循环调用的。</span><br><span class="hljs-function"><span class="hljs-keyword">function</span>&nbsp;<span class="hljs-title">saveImg</span>&nbsp;(<span class="hljs-params">title,&nbsp;imgUrl</span>)&nbsp;&nbsp;</span>{<br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="hljs-keyword">var</span>&nbsp;imgName&nbsp;=&nbsp;title&nbsp;+&nbsp;<span class="hljs-string">\'.jpg\'</span>;&nbsp;<span class="hljs-comment">//&nbsp;根据实际需求设置</span><br>&nbsp;&nbsp;&nbsp;&nbsp;request.head(imgUrl,&nbsp;<span class="hljs-function"><span class="hljs-keyword">function</span>&nbsp;(<span class="hljs-params">err,&nbsp;res,&nbsp;body</span>)&nbsp;</span>{<br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="hljs-keyword">if</span>&nbsp;(err)&nbsp;{<br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="hljs-built_in">console</span>.log(err);<br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;}<br>&nbsp;&nbsp;&nbsp;&nbsp;});<br>&nbsp;&nbsp;&nbsp;&nbsp;request(imgUrl).pipe(fs.createWriteStream(<span class="hljs-string">\'./img/\'</span>&nbsp;+&nbsp;imgName));&nbsp;<span class="hljs-comment">//&nbsp;用&nbsp;stream&nbsp;保存图片</span><br>}<br><br><span class="hljs-comment">//&nbsp;开始运行</span><br>main();<br></code></pre>\n' +
            '<p>例子中的代码并不能直接运行，并不建议直接复制，可以一点点打进去，理解一下其中的意思。然后一点点修改。等到理解了它的意思就会觉得这个爬虫很容易，也完全不必在意 saveFile 和 saveImg 方法，想要什么效果，完全可以自己去写。</p></div></div>'},
        {id: 2, title: 'js运行机制', src: './../assets/images/carousel/2.jpg', time: '2018-07-22', texttop: '爬虫是指根据一定的规则，自动抓取网站上的程序或脚本。node 也可以实现爬虫。今天要介绍的就是简单的爬虫'},
        {id: 3, title: '浏览器渲染机制', src: './../assets/images/carousel/3.jpg', time: '2018-07-22', texttop: '爬虫是指根据一定的规则，自动抓取网站上的程序或脚本。node 也可以实现爬虫。今天要介绍的就是简单的爬虫'},
        {id: 4, title: 'nodejs爬虫', src: './../assets/images/carousel/1.jpg', time: '2018-07-22', texttop: '爬虫是指根据一定的规则，自动抓取网站上的程序或脚本。node 也可以实现爬虫。今天要介绍的就是简单的爬虫'},
        {id: 5, title: 'js运行机制', src: './../assets/images/carousel/2.jpg', time: '2018-07-22', texttop: '爬虫是指根据一定的规则，自动抓取网站上的程序或脚本。node 也可以实现爬虫。今天要介绍的就是简单的爬虫'}
      ],
      tagclass: [
        {name:'html', color:0, count:8}, {name:'css', color:1, count:8}, {name:'js', color:2, count:8}, {name:'vue', color:3, count:8}, {name:'nodejs', color:4, count:8},
        {name:'php', color:0, count:8}, {name:'Asp.net', color:1, count:8}, {name:'sqlserver', color:2, count:8}, {name:'mysql', color:3, count:8}, {name:'Android', color:4, count:8},
        {name:'elementui', color:0, count:8}, {name:'mintui', color:1, count:8}, {name:'python', color:2, count:8}, {name:'java', color:3, count:8}, {name:'smail', color:4, count:8}
      ]
    }
  },
  methods: {
    showBlog (id) {
      let arr = this.carousel
      for (let item in arr) {
        if (arr[item].id == id) {
          this.showItem = arr[item]
        }
      }
    }
  },
  mounted () {
    let id = this.$router.history.current.params.id
    if (id) {
      this.flagCarousel = false
      this.showBlog(id)
    }else {
      this.flagCarousel = true
    }
  },
  watch: {
    '$route' (to, from) {
      // 对路由变化作出响应...
      let id = to.params.id
      if (id) {
        this.flagCarousel = false
        this.showBlog(id)
      }else {
        this.flagCarousel = true
      }
    }
  }
}
</script>

<style scoped lang="less">
  .blog-header, .blog-footer {
    background-color: #00BCD4;
    color: #f7f7f7;
    text-align: center;
    line-height: 60px;
    opacity: 0.7;
  }
  .blog-header ul{
    overflow: hidden;
    margin: 0 auto;
  }
  .blog-header .item{
    min-width: 60px;
    float: left;
    list-style: none;
  }
  .blog-con{
    margin: 10px auto 10px;
    min-height: 602px;
    background-color: #333;
    opacity: 0.8;
  }
  .blog-con-left,.blog-con-right{
    float: left;
    min-height: 800px;
  }
  .blog-con-left{
    width: 35%;
    /*min-height: 500px;*/
    box-shadow: 0 0px 10px 0 #ccc;
  }
  .blog-con-left-header{
    height: 200px;
    overflow: hidden;
    background: #05192e;
  }
  .blog-con-left-header .head{
    height: 80px;
    width: 80px;
    margin-top: 25px;
    display: inline-block;
    border-radius: 50%;
    background-color: #222222;
    background-image: url("./../assets/images/head.jpeg");
    background-position: 50% 50%;
    background-size: 100%;
  }
  .blog-con-left-con{
    margin-top: 20px;
  }
  .blog-con-right{
    margin-left: 1%;
    width: 63%;
  }
  .blog-footer span{
    margin: 4px;
  }
  .el-carousel__item h3 {
    width: 100%;
    position: absolute;
    bottom: 10px;
    font-size: 16px;
    color: #fff;
    -webkit-text-shadow: 0 0 10px #333;
    -moz-text-shadow: 0 0 10px #333;
    text-shadow: 0 0 10px #333;
  }

  .el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
  }

  .el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
  }

  .el-tag{
    margin: 4px;
    cursor: pointer;
  }
  @media screen and (min-width: 1400px){
    .blog-con,.blog-header ul{
      width: 1200px;
    }
  }
  @media screen and (min-width: 1000px) and (max-width: 1400px){
    .blog-con,.blog-header ul{
      width: 800px;
    }
  }
  @media screen and (max-width: 600px){
    .blog-con,.blog-header ul{
      width: 100%;
    }
    .blog-con-left{
      display: none;
    }
    .blog-con-right{
      width: 100%;
      margin-left: 0;
      padding: 0 1px;
    }
    .el-carousel__item--card.is-active{
      width: 100% !important;
      transform: translateX(0) scale(1) !important;
    }
    .el-carousel__item:nth-child(n) {
      /*width: 0;*/
    }
  }
</style>



