<template>
    <section class="blogitem">
      <header>
        <h4 class="blogitem-title">{{item.title}}</h4>
        <p class="blogitem-time">{{item.time}}</p>
        <p class="subtitle">前言</p>
        <p class="blogitem-texttop">{{item.texttop}}</p>
        <p class="subtitle">正文</p>
      </header>
      <div class="text" v-html="item.text"></div>
    </section>
</template>

<script>
  export default {
    name: 'BlogShow',
    props: ['item']
  }
</script>

<style lang="less" scoped>
  .blogitem-color{
    color: rgb(62,62,62);
  }
  .blogitem{
    background-color: #607D8B;
    overflow: hidden;
    margin-bottom: 1px;
    padding-bottom: 10px;
  }
  .blogitem .blogitem-title{
    border-left:10px solid #02a8f5;
    margin: 6px;
    text-align: left;
    padding-left: 4px;
    font-size: 1.2rem;
    .blogitem-color
  }
  .blogitem .blogitem-time,.blogitem-texttop{
    text-align: left;
    padding-left: 5px;
    font-size: .9rem;
    .blogitem-color
  }
  .blogitem .subtitle{
    border-left: 5px solid #59c8ff;
    line-height: 1.2em;
    margin: 6px;
    padding-left: 4px;
    font-size: 1.1em;
    text-indent: 0;
    text-align: left;
    color: #333;
  }
  .blogitem .text{
    text-align: left;
    margin: 6px;
    padding-left: 0px;
    color: #f7f7f7;
  }
</style>



